export const years = () => {
  let currentYear = new Date().getFullYear();
  let _years = []
  for (let i = 0; i <= 20; i++) {
    _years.push(currentYear);
    currentYear++;
  }
  return _years;
}

export const months = (format) => {
  let _months = [];
  moment.locale('tr');
  for (var i = 0; i < 12; i++) {
    _months.push(moment().months(i).format(format));
  }
  return _months;
}