import React, { Component } from 'react';
import { Table, Popover } from 'antd';
import { InlineSearch, PolicyOperations } from '../../UIComponents'
import { myPolicies } from '../../../mock'

class PolicyList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          title: 'Polie Numaras',
          dataIndex: 'policyNumber',
          render: (text, { info }) => (
            <span>
              {
                info &&
                <Popover
                  content={info.text}
                  title={info.title}
                  trigger="hover"
                  placement="rightTop"
                  overlayClassName="table-popover"
                >
                  <i className="fas fa-info info"></i>
                </Popover>
              }
              {text}
            </span>
          ),
          sorter: (a, b) => a.policyNumber.localeCompare(b.policyNumber),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Sigorta rn',
          dataIndex: 'insuranceProduct',
          sorter: (a, b) => a.insuranceProduct.localeCompare(b.insuranceProduct),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Ara Plaka',
          dataIndex: 'plate',
          sorter: (a, b) => a.plate.localeCompare(b.plate),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Sigorta irketi',
          dataIndex: 'insuranceCompany',
          sorter: (a, b) => a.insuranceCompany.localeCompare(b.insuranceCompany),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Balang Tarihi',
          dataIndex: 'startDate',
          sorter: (a, b) => a.startDate.localeCompare(b.startDate),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Biti Tarihi',
          dataIndex: 'endDate',
          sorter: (a, b) => a.endDate.localeCompare(b.startDate),
          sortDirections: ['descend', 'ascend'],
        },
        {
          title: 'Polie Birimi',
          dataIndex: 'policyUnit',
          sorter: (a, b) => a.policyUnit.localeCompare(b.policyUnit),
          sortDirections: ['descend', 'ascend'],
        },
      ],
      selected: null,
    }
  }


  render() {
    let { columns, selected } = this.state
    return (
      <div className="policies">
        {
          selected ?
            <div className="selected-info">
              Polienizin biti tarihi 29 gnden uzun olduu iin teklif almamaktadr. Polieniz ile ilgili bilgi almak istiyorsanz 
              <span onClick={() => this.props.history.push('/talepler/taleplerim')}> Taleplerim </span> mensnden 
              <span onClick={() => this.props.history.push('/talepler/yeni-talep')}> Yeni Talep </span> admndan bize ulabilirsiniz.
            </div>
            :
            <div className="text-center">
              <h2>Polielerim</h2>
              <p>Polie kaytlarna tklayarak ilem adnz seebilir, polie detaylarna bakabilir, plaka bilgi gncellemesi yapabilir, yeni teklif talebinde bulunabilir, teklfinizi polieletirebilir veya polienizi kendinize e-posta olarak gnderebilirsiniz.</p>
            </div>

        }
        <Table
          className="mt-40 stretch-table stripped-table"
          columns={columns}
          dataSource={myPolicies}
          pagination={false}
          expandRowByClick
          scroll={{x:480}}
          rowSelection={{
            type: "radio",
            selectedRowKeys: selected ? selected.key : null
          }}
          onRow={(item) => {
            return {
              onClick: () => this.setState({ selected: item })
            }
          }
          }
          title={() =>
            <div className="d-flex justify-content-space-between align-items-center pl-60 pr-50">
              <h3 className="m-0 text-white">
                Polie Tablosu
              </h3>
              <InlineSearch className="d-inline-flex" />
            </div>
          }
        />
        {
          <PolicyOperations item={selected} className="mt-20" />

        }
        <p className="mt-20">
          Deerli mterimiz; Onaylanan polieler sigorta irketlerinden 3 i gn iinde yansmaktadr. 3 gn iinde yansmayan polielerinizin olmas durumunda tarafmza ulaabilirsiniz.
          </p>

        <p className="mt-20 text-blue text-bold">
          vdf_Sigortadestek@vdf.com.tr
          </p>
      </div>
    );
  };
};

export default PolicyList;