import React, { Component } from 'react';
import { Modal, Button, Icon, Row, Col, Card, Radio, Form, Input, Select } from 'antd';
import { days, months, years } from '../../helpers';

class LoginButton extends Component {
  constructor(props) {
    super(props);
    let today = new Date();
    this.state = {
      visible: false,
      accountType: 1,
      tckn: '',
      year: today.getFullYear(),
      month: today.getMonth() + 1,
      day: today.getDate(),
      birthday: '',
      taxNumber: '',
      customerNumber: ''
    };
  };

  toggle = () => {
    this.setState({ visible: !this.state.visible });
  };

  dateChange = (e, key) => {
    this.setState({ [key]: e });
  };

  login = (e) => {
    e.preventDefault();
  };

  render() {
    return (
      <div>
        <Button size="small" onClick={this.toggle}>
          <Icon type="login" />
          ye Girii
        </Button>
        <Modal className="login-modal" width={1120} visible={this.state.visible} footer={null} onCancel={this.toggle}>
          <div className="background">
            <div className="bg-left" />
          </div>
          <Row gutter={100} className="login-box">
            <Col xs={24} md={12}>
              <Card title={
                <React.Fragment>
                  <h1>ye Girii</h1>
                  <span>Aadaki bilgileri doldurarak tm hizmet ve zelliklerden faydalanabilirsiniz.</span>
                </React.Fragment>
              }>
                <Form onSubmit={this.login}>
                  <Form.Item>
                    <Radio.Group onChange={(e) => this.setState({ accountType: e.target.value })} value={this.state.accountType}>
                      <Radio value={1}>Bireysel</Radio>
                      <Radio value={2}>Tzel</Radio>
                    </Radio.Group>
                  </Form.Item>
                  <Form.Item label="T.C. Kimlik Numaran">
                    <Input placeholder="rn : 11100110011" value={this.state.tckn} onChange={(e) => this.setState({ tckn: e.target.value })} />
                  </Form.Item>
                  <Form.Item label="Doum Tarihiniz">
                    <Row gutter={20}>
                      <Col span={8}>
                        <Select placeholder="Gn" value={this.state.day} onChange={(e) => this.dateChange(e, 'day')}>
                          {days(this.state.month, this.state.year).map(x => (
                            <Select.Option value={x} key={x}>{x}</Select.Option>
                          ))}
                        </Select>
                      </Col>
                      <Col span={8}>
                        <Select placeholder="Ay" value={this.state.month} onChange={(e) => this.dateChange(e, 'month')}>
                          {months.map(x => (
                            <Select.Option value={x.value} key={x.value}>{x.name}</Select.Option>
                          ))}
                        </Select>
                      </Col>
                      <Col span={8}>
                        <Select placeholder="Yl" value={this.state.year} onChange={(e) => this.dateChange(e, 'year')}>
                          {years().map(x => (
                            <Select.Option value={x} key={x}>{x}</Select.Option>
                          ))}
                        </Select>
                      </Col>
                    </Row>
                  </Form.Item>
                  <Form.Item>
                    <Button className="full-width" type="primary">Giri Yap</Button>
                  </Form.Item>
                </Form>
              </Card>
            </Col>
            <Col xs={24} md={12} className="login-banner">
              <h3 className="text-blue text-center text-thick mb-40">Krediniz in</h3>
              <Row gutter={20} className="mb-40">
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/cash.svg" alt="" />
                  <p className="text-thick">Kredi bilgilerini grntleyebilir</p>
                </Col>
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/calculator.svg" alt="" />
                  <p className="text-thick">Erken kapama tutarn hesaplayabilir</p>
                </Col>
              </Row>
              <Row gutter={20} className="mb-40">
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/clock.svg" alt="" />
                  <p className="text-thick">Kredi demelerini grebilir</p>
                </Col>
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/notes.svg" alt="" />
                  <p className="text-thick">deme planna eriebilirsiniz</p>
                </Col>
              </Row>
              <h3 className="text-blue text-center text-thick mb-40">Sigorta Polieniz in</h3>
              <Row gutter={20}>
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/grammar.svg" alt="" />
                  <p className="text-thick">Polie detaylarn grntleyebilirsiniz</p>
                </Col>
                <Col span={12} className="text-center">
                  <img src="/static/img/icons/bookmark.svg" alt="" />
                  <p className="text-thick">ade ve iptal talebinde bulunabilir</p>
                </Col>
              </Row>
            </Col>
          </Row>
        </Modal>
      </div>
    );
  };
};

export default LoginButton;